* {
  margin: 0;
  padding: 0;
  font-family: system-ui;
  box-sizing: border-box;
}

nav {
  background: #231f20;
  color: white;
  height: 66px;
  display: flex;
  justify-content: space-between;
  padding: 0.5rem calc((100vw - 1000px) / 2);
  z-index: 10;
  justify-content: flex-start;
  /* stick the position: */
  position: sticky;
  top: 0px;
}

.navPopUpWrapper {
  background-color: #FFFFFF;
  position: absolute;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  top: 51px;
}
.navDropDownContentProfiles {
  position: absolute;
  background-color: #f9f9f9;
  min-width: 170px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}
.navDropDownContentLanguage {
  position: absolute;
  background-color: #f9f9f9;
  min-width: 110px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.navDropDownContentProfiles div {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}
.navDropDownContentLanguage div {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.navDropDownContentProfiles div:hover {
  background-color: #fff995;
}

.navDropDownContentLanguage div:hover {
  background-color: #fff995;
}

.nav-logo {
  color: #ffff;
  display: flex;
  align-items: center;
  text-decoration: none;
  cursor: pointer;
}

.nav-menu {
  display: flex;
  align-items: center;
  margin-right: -24px;
  width: 100vw;
  white-space: nowrap;
}

.nav-button {
  display: flex;
  align-items: center;
  margin-right: 24px;
  justify-content: flex-end;
  width: 100vw;
}

.nav-button-link {
  border-radius: 4px;
  background: #fff995;
  padding: 10px 22px;
  color: #000;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  margin: 3px;
}

.nav-button-link:hover {
  transition: all 0.2s ease-in-out;
  background: #fff;
  color: #010606;
}

.navBarText {
    margin-left: 5px;
    font-size: 14px;
 }


input::-webkit-calendar-picker-indicator {
  cursor: pointer;
}



